<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-button type="primary" @click="addWebsiteState()"></el-button>
      <el-dialog title="" :visible.sync="addWebsiteVisible" width="50%" @close="resetForm()">
        <el-form :model="websitesInfo" :rules="addruleForm" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="websiteName">
            <el-input v-model="websitesInfo.websiteName"></el-input>
          </el-form-item>
          <el-form-item label="url" prop="url">
            <el-input v-model="websitesInfo.url"></el-input>
          </el-form-item>
          <el-form-item label="" prop="scrapySelect">
            <el-select v-model="websitesInfo.scrapySelect" placeholder="">
              <el-option v-for = "(item,index) in scrapy" :label= "item" :value = "item" :key = "index"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addWebsiteVisible = false"> </el-button>
          <el-button type="warning" @click="testWebsite(websitesInfo.url)"></el-button>
          <el-button type="primary" @click="addWebsite()"></el-button>
        </span>
      </el-dialog>
      <el-table :data="websites" stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="websiteName" label=""> </el-table-column>
        <el-table-column prop="url" label="url"> </el-table-column>
        <el-table-column prop="state" label=""></el-table-column>
        <el-table-column prop = "offOrOn" label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.offOrOn" @change="stateOffOrOn(scope.row)">
            </el-switch>
          </template>
        </el-table-column>
        <el-table-column prop="scrapySelect" label=""></el-table-column>
        <el-table-column prop="scrapyState" label=""></el-table-column>
        <el-table-column prop = "scrapyOffOrOn" label="">
          <template slot-scope="scope">
            <el-switch v-model="scope.row.scrapyOffOrOn" @change="scrapyStateOffOrOn(scope.row)"></el-switch>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button type="primary" icon="el-icon-edit" size="mini" @click="editWebsite(scope.row)"></el-button>
            <el-dialog title="" :visible.sync="editWebsiteVisible" width="50%" @close="resetEditForm()">
              <el-form :model="editWebsiteInfo" :rules="editRuleForm" ref="editFormRef" label-width="100px" >
                <el-form-item label="" prop="websiteName">
                  <el-input v-model="editWebsiteInfo.websiteName"></el-input>
                </el-form-item>
                <el-form-item label="url" prop="url">
                  <el-input v-model="editWebsiteInfo.url"></el-input>
                </el-form-item>
                <el-form-item label="" prop="scrapySelect">
                  <el-select v-model="editWebsiteInfo.scrapySelect" placeholder="">
                    <el-option v-for = "(item,index) in scrapy" :label= "item" :value = "item" :key = "index"></el-option>
                  </el-select>
                </el-form-item>
              </el-form>
              <span slot="footer" class="dialog-footer">
                <el-button @click="editWebsiteVisible = false"></el-button>
                <el-button type="primary" @click="editWebsiteSubmit (editWebsiteInfo)"> </el-button>
              </span>
            </el-dialog>
            <el-button type="danger" icon="el-icon-delete" size="mini" @click="deleteWebsiteInfo(scope.row)"></el-button>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      editWebsiteInfo: {
        websiteName: '',
        url: '',
        scrapySelect: ''
      },
      scrapy: ['', ''],
      // 

      editWebsiteVisible: false,
      // 
      addWebsiteVisible: false,
      websites: [{
        websiteName: '',
        url: '',
        state: '',
        offOrOn: false,
        scrapySelect: '',
        scrapyState: '',
        scrapyOffOrOn: false
      }],
      websitesInfo: {
        websiteName: '',
        url: '',
        state: '',
        scrapySelect: ''
      },
      addruleForm: {
        websiteName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        url: [
          { required: true, message: 'url', trigger: 'blur' }
        ],
        scrapySelect: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      editRuleForm: {
        websiteName: [
          { required: true, message: '', trigger: 'blur' }
        ],
        url: [
          { required: true, message: 'url', trigger: 'blur' }
        ],
        scrapySelect: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }

    }
  },
  created () {
    this.getWebsitelist()
  },
  methods: {
    resetEditForm () {
      // 
      console.log(this.$refs.editFormRef)
      this.$refs.editFormRef.resetFields()
    },
    async getScrapyList () {
      // 
      const { data: res } = await this.$http.get('selectScrapys')
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.scrapy = res.entity
    },
    addWebsiteState () {
      // 
      this.addWebsiteVisible = true
      this.getScrapyList()
    },
    editWebsiteSubmit (WebsiteInfo) {
      // 
      this.$refs.editFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('updateWebsite', WebsiteInfo)
        if (res.code !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getWebsitelist()
        this.editWebsiteVisible = false
      })
    },

    editWebsite (website) {
      // 
      this.editWebsiteVisible = true
      this.editWebsiteInfo = website
    },
    async deleteWebsiteInfo (WebsiteInfo) {
      // 
      console.log(WebsiteInfo)
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const params = new URLSearchParams()
      params.append('url', WebsiteInfo.url)
      const { data: res } = await this.$http.post('deleteWebsite', params)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
      this.getWebsitelist()
    },
    addWebsite () {
      // 
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('addWebsite', this.websitesInfo)
        if (res.code !== 200) {
          return this.$message.error('')
        }
        this.$message.success('')
        this.getWebsitelist()
        this.resetForm()
        this.addWebsiteVisible = false
      })
    },
    resetForm () {
      // 
      this.$refs.addFormRef.resetFields()
    },
    async getWebsitelist () {
      // 
      const { data: res } = await this.$http.get('listWebsites')
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.websites = res.entity
    },
    async stateOffOrOn (scope) {
      // 
      const params = new URLSearchParams()
      params.append('url', scope.url)
      const { data: res } = await this.$http.post('updateOffOrOn', params)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      setTimeout(callback => {
        this.getWebsitelist()
      }, 2000)
    },
    async testWebsite (url) {
      // 
      const params = new URLSearchParams()
      params.append('url', url)
      const { data: res } = await this.$http.post('testWebsiteIsOk', params)
      if (res.code !== 200) {
        return this.$message.error('url')
      }
      this.$message.success('url')
    },

    async scrapyStateOffOrOn (website) {
      // 
      const params = new URLSearchParams()
      params.append('scrapyName', website.scrapySelect)
      const { data: res } = await this.$http.post('openScrapyInstance', params)
      if (res.code === 500) {
        return this.$message.error('')
      }
    }
  }
}
</script>

<style scoped>
.el-breadcrumb{
  margin-bottom:15px;
  font-size: 12px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0,0,0,0.25)!important;
}

</style>
