<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <el-card>
    <el-button type = "primary" @click="addScrapyInstance()"></el-button>
    <span>|</span>
    <el-button type = "warning" @click = "vedioVisible = true"></el-button>
    <el-dialog title = "" :visible.sync= "vedioVisible" width="50%">
      <div>
        <video-player :options="playerOptions"></video-player>
      </div>
    </el-dialog>
    <el-dialog title="" :visible.sync="addScrapyInstanceVisible" width="50%" @close="resetScrpayInstanceForm()">
      <el-form :model="scrapyInstanceInfo" :rules="addScrapyruleForm" ref="addScrapyInstanceRef" label-width="100px">
        <el-form-item label="" prop="additionalInfo">
          <el-input v-model="scrapyInstanceInfo.additionalInfo" ></el-input>
        </el-form-item>
        <el-form-item label ="canal-IP">
          <el-input v-model="scrapyInstanceInfo.serverAddress" ></el-input>
        </el-form-item>
        <el-form-item label ="canal-Port">
          <el-input v-model="scrapyInstanceInfo.port" ></el-input>
        </el-form-item>
         <el-form-item label ="canal-">
          <el-input v-model="scrapyInstanceInfo.destination" ></el-input>
        </el-form-item>
         <el-form-item label ="Kafka-Topic">
          <el-input v-model="scrapyInstanceInfo.topic" ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
          <el-button @click="addScrapyInstanceVisible = false"> </el-button>
          <el-button type="primary" @click="addDBInstanceSubmit()"></el-button>
      </span>
    </el-dialog>
    <el-table :data="canals" stripe border style="width: 100%">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="additionalInfo" label=""></el-table-column>
      <el-table-column prop="serverAddress" label="canal-Ip"></el-table-column>
      <el-table-column prop="port" label="canal-Port"></el-table-column>
      <el-table-column prop="destination" label=""></el-table-column>
      <el-table-column prop="topic" label="Kafka-Topic"></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="primary" icon="el-icon-edit" @click="editScrapyInstance(scope.row)"></el-button>
          <el-button type="danger" icon="el-icon-delete" @click ="deleteScrapyInstance(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      canals: '',
      vedioVisible: false,
      addScrapyInstanceVisible: false,
      playerOptions: {
        playbackRates: [0.7, 1.0, 1.5, 2.0], // 
        autoplay: false, // true,
        controls: true, // 
        preload: 'auto', // 
        muted: false, // 
        loop: false, // 
        language: 'zh-CN',
        aspectRatio: '16:9', //  - "16:9""4:3"
        fluid: true, // trueVideo.js player
        sources: [{
          type: 'video/mp4',
          src: 'http://localhost:8080/file/video'// 
        }],
        // poster: 'http://39.106.117.192:8080/static/indexImg.png', // 
        width: document.documentElement.clientWidth,
        notSupportedMessage: '' // Video.js
      },
      // 
      scrapyInstanceInfo: {
        additionalInfo: '1',
        serverAddress: '2',
        port: '3',
        destination: '4',
        topic: '5'
      },
      addScrapyruleForm: {
        additionalInfo: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    }
  },
  created () {
    this.getCanalList()
  },
  methods: {
    async getCanalList () {
      const { data: res } = await this.$http.get('listCanal')
      console.log(res)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.canals = res.entity
    },
    addScrapyInstance () {
      this.addScrapyInstanceVisible = true
    },
    editScrapyInstance (scrapy) {
      console.log(scrapy)
    },
    deleteScrapyInstance (scrapy) {
      console.log(scrapy)
    },
    resetScrpayInstanceForm () {
      this.$refs.addScrapyInstanceRef.resetFields()
    },
    addDBInstanceSubmit () {
      this.$refs.addScrapyInstanceRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('addCanal', this.scrapyInstanceInfo)
        if (res.code !== 200) return this.$message.error('')
        this.$message.success('')
        this.getCanalList()
        this.addScrapyInstanceVisible = false
      })
    }
  }
}
</script>
<style scoped>
.el-breadcrumb{
  margin-bottom:15px;
  font-size: 12px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0,0,0,0.25)!important;
}
</style>
