<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-table :data = 'alarmslist' stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="dataInfo" label=""> </el-table-column>
        <el-table-column prop="errorUrl" label="url"> </el-table-column>
        <el-table-column prop="errorSiteName" label=""></el-table-column>
        <el-table-column prop="errorLog" label=""></el-table-column>
        <el-table-column prop="time" label=""></el-table-column>
        <el-table-column prop="additionalInfo" label=""></el-table-column>
      </el-table>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      alarmslist: ''
    }
  },
  created () {
    this.getAlarmsList()
  },
  methods: {
    async getAlarmsList () {
      const { data: res } = await this.$http.get('listDataErrorList')
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.alarmslist = res.entity
    }
  }
}
</script>

