<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-button type="primary" @click="addDeviceVisible = true"></el-button>
      <el-dialog title="" :visible.sync="addDeviceVisible" width="50%"  @close="resetForm()">
        <el-form v-model="systemInfo" :rules="addruleForm" ref="addFormRef" label-width="100px">
          <el-form-item label="" prop="systemType">
            <el-select v-model="systemInfo.systemType" placeholder="">
              <el-option  v-for= "item in systemTypeOption" :key="item.index" :label ="item.label" :value = "item.name" @click = "console.log(item)"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="systemName">
            <el-input v-model="systemInfo.systemName"></el-input>
          </el-form-item>
          <el-form-item label="ip/url" prop="ip">
            <el-input v-model="systemInfo.ip"></el-input>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="addDeviceVisible = false"> </el-button>
          <el-button type="warning" @click="testIpAndPort(systemInfo)"></el-button>
          <el-button type="primary" @click="addSystem()"></el-button>
        </span>
      </el-dialog>
      <el-table :data="device" stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="systemName" label=""> </el-table-column>
        <el-table-column prop="systemType" label=""> </el-table-column>
        <el-table-column prop="ip" label="ip"></el-table-column>
        <el-table-column prop="state" label="">
          <!-- <template slot = "scope">
            <el-switch v-model="scope.row.state" @change="systemStateChange(scope.row)">
            </el-switch>
          </template> -->
        </el-table-column>
      </el-table>
      <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :page-sizes="[1, 2, 5, 10]"
      :current-page="queryInfo.pagenum"
      :page-size="queryInfo.pagesize"
      :total = total
      layout="total, sizes, prev, pager, next, jumper"
      >
    </el-pagination>
    </el-card>
    </div>
</template>

<script>
export default {
  data () {
    return {
      // 
      addDeviceVisible: false,
      total: 0,
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 5
      },

      device: [{
        systemType: 'hhh',
        ip: 'url',
        systemName: 'hhh',
        port: 0,
        state: 'hhhh'
      }],
      systemTypeOption: [
        { label: '', index: '1', name: '' },
        { label: '', index: '2', name: '' },
        { label: '', index: '3', name: '' },
        { label: '', index: '4', name: '' }
      ],
      systemInfo: {
        // 
        systemType: '',
        systemName: '',
        ip: ''
      },
      addruleForm: {
        systemName: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 10, message: ' 3  5 ', trigger: 'blur' }
        ],
        ip: [
          { required: true, message: 'ip', trigger: 'blur' }
        ],
        systemType: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }

    }
  },
  created () {
    this.getDevicelist()
  },
  methods: {
    async getDevicelist () {
      const { data: res } = await this.$http.get('listSystems')
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.device = res.entity
    },
    handleSizeChange (val) {
      this.queryInfo.pagesize = val
      this.getDevicelist()
    },
    handleCurrentChange (val) {
      this.queryInfo.pagenum = val
      this.getDevicelist()
    },
    resetForm () {
      this.$refs.addFormRef.resetFields()
    },
    async systemStateChange (state) {
      this.device.state = state
      console.log(state)
    },
    addSystem () {
      console.log(this.systemInfo)
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const { data: res } = await this.$http.post('addRemoteSystemIsOk', this.systemInfo)
        if (res.code !== 200) return this.$message.error('')
        this.$message.success('')
        this.addDeviceVisible = false
      })
    },
    async testIpAndPort (deviceinfo) {
      console.log(deviceinfo)
      this.addDeviceVisible = false
    }
  }
}
</script>

<style scoped>
.el-breadcrumb{
  margin-bottom:15px;
  font-size: 12px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0,0,0,0.25)!important;
}
</style>
