<template>
  <div>
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-card >
      <el-table :data = 'canalList' stripe border style="width: 100%">
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="additionalInfo" label=""></el-table-column>
        <el-table-column prop="serverAddress" label="canal-Ip"></el-table-column>
        <el-table-column prop="port" label="canal-Port"></el-table-column>
        <el-table-column prop="destination" label=""></el-table-column>
        <el-table-column prop="topic" label="Kafka-Topic"></el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-tooltip content="" placement="top">
              <el-button type="success" size="mini" @click="openInstance(scope.row)" icon = "el-icon-switch-button"></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top">
              <el-button type="primary" size="mini" @click="dataStore(scope.row)" icon="el-icon-refresh"></el-button>
            </el-tooltip>
            <el-tooltip content="" placement="top">
              <el-button type="danger" size="mini" @click="stopInstance(scope.row)" icon="el-icon-circle-close"></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
      <el-dialog title="" :visible.sync= "dataStoreVisible" width="50%" @close="resetForm()">
        <el-form :model="dropInfo" :rules= "addruleForm" ref="addFormRef" label-width="100px" margin-top="3px">
          <el-card v-for="(item,index) in dropInfo.tableList" :key="index" style="margin:2px">
            <el-button v-if="index!==0" type="danger" @click="removeNewTag(index)" icon="el-icon-delete" size="mini"></el-button>
            <el-button v-else type="primary" @click="addNewDrop()" size="mini">+</el-button>
            <el-form-item prop ="item.tableName" label=":" >
              <el-input v-model="dropInfo.tableList[index].tableName" placeholder=""></el-input>
            </el-form-item>
            <el-form-item prop="item.syncOperate" label="">
                <el-checkbox-group v-model="dropInfo.tableList[index].syncOperate" size="mini">
                  <el-checkbox label="1" border></el-checkbox>
                  <el-checkbox label="2" border></el-checkbox>
                  <el-checkbox label="3" border></el-checkbox>
                  <el-checkbox label="4" border></el-checkbox>
                  <el-checkbox label="5" border></el-checkbox>
                </el-checkbox-group>
              </el-form-item>
          </el-card>
          <el-row>
            <el-col></el-col>
            <e-col><el-input-number size="mini" v-model="dropInfo.time.day" controls-position="right" :min="0"></el-input-number><span></span></e-col>
            <e-col><el-input-number size="mini" v-model="dropInfo.time.hour" controls-position="right" :min="0"></el-input-number><span></span></e-col>
            <e-col><el-input-number size="mini" v-model="dropInfo.time.min" controls-position="right" :min="0"></el-input-number><span></span></e-col>
            <e-col><el-input-number size="mini" v-model="dropInfo.time.second" controls-position="right" :min="0"></el-input-number><span></span></e-col>
          </el-row>
        </el-form>
        <span slot="footer">
          <el-button @click="dataStoreVisible = false"> </el-button>
          <el-button type="primary" @click="dataStoreSubmit(dropInfo)"></el-button>
        </span>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
export default {
  data () {
    return {
      canalList: [
        {
          topic: '1'
        }
      ],
      // 
      // 
      dataStoreVisible: false,
      // 
      stringInfo: '',
      dropInfo: {
        tableList: [
          {
            tableName: '',
            syncOperate: []
          }
        ],
        topic: '',
        time: {
          day: '',
          hour: '',
          min: '',
          second: ''
        }
      }
    }
  },
  created () {
    this.getCanalList()
    this.getDBTableList()
  },
  methods: {
    async getCanalList () {
      const { data: res } = await this.$http.get('listCanal')
      console.log(res)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.canalList = res.entity
    },
    async dataStore (website) {
      this.dataStoreVisible = true
      console.log(website)
      this.dropInfo.topic = website.topic
    },
    async dataStoreSubmit (dropInfo) {
      // datastore

      const time = (dropInfo.time.second) + dropInfo.time.min * 60 + dropInfo.time.hour * 3600 + dropInfo.time.day * 3600 * 24
      const params = {}
      params.seconds = time
      params.topic = dropInfo.topic
      params.tableList = dropInfo.tableList
      console.log(params)
      const { data: res } = await this.$http.post('addConsumer', params)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    resetForm () {
      // 
      console.log(this.$refs.addFormRef)
      this.$refs.addFormRef.resetFields()
    },
    async openInstance (instance) {
      console.log(instance)
      const { data: res } = await this.$http.post('startCanalInstance', instance)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    async stopInstance (instance) {
      const params = new URLSearchParams()
      params.append('topic', instance.topic)
      const { data: res } = await this.$http.post('stopKafkaConsumer', params)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      return this.$message.success('')
    },
    removeNewTag (index) {
      this.dropInfo.tableList.pop(index)
    },
    addNewDrop () {
      this.dropInfo.tableList.push({
        tableName: '',
        syncOperate: []
      })
    }
  }
}
</script>

<style scoped>
.el-breadcrumb{
  margin-bottom:15px;
  font-size: 12px;
}
.el-card{
  box-shadow: 0 1px 1px rgba(0,0,0,0.25);

}
.el-checkbox{
  margin: 1px;
}
