<template>
<div>
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/welcome' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
  <el-card>
    <el-upload action="http://localhost:8080/uploadFile">
      <el-button type="primary"></el-button>
    </el-upload>
    <el-table :data="fileInfo" stripe border style="width: 100%">
      <el-table-column type="index"></el-table-column>
      <el-table-column prop="fileName" label=""> </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button icon = "el-icon-download" @click = 'download(scope.row)'></el-button>
        </template>
      </el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button type="danger" icon="el-icon-delete" @click = "deleteFile(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>

</div>
</template>

<script>
export default {
  data () {
    return {
      addFileVisible: false,
      // list
      file: [],
      // file
      fileInfo: [{
        fileName: '',
        fileUrl: 'Url'
      }, {
        fileName: '',
        fileUrl: ' Url'
      }]
    }
  },
  created () {
    this.getFileList()
  },
  methods: {
    async getFileList () {
      const { data: res } = await this.$http.get('listFiles')
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.fileInfo = res.entity
    },
    addFile () {
      this.addFileVisible = true
    },
    resetForm () {

    },
    // 
    // 
    async download (File) {
      const { data: res } = await this.$http.get(`download?id=${File.id}`)
      console.log(res)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.$message.success('')
    },
    // 
    async deleteFile (File) {
      const params = new URLSearchParams()
      params.append('id', File.id)
      const { data: res } = await this.$http.post('deleteFile', params)
      if (res.code !== 200) {
        return this.$message.error('')
      }
      this.getFileList()
      return this.$message.success('')
    }
  }
}
</script>

